import React from "react";
import { useSelector } from "react-redux";
import FoliaCarousel from "./FoliaCarousel";
import BarraServicos from "../BarraServicos";
import AppFooter from "../AppFooter.jsx";
import { MDBRow, MDBCol, MDBAnimation } from "mdbreact";

export default function Home() {
  let produtos = useSelector(state => state.products.produtos);
  produtos = produtos.filter(produto => {
    return produto.type === "grouped";
  });
  const camarotes = produtos.filter(produto => {
    return produto.categories[0].id === 16;
  });
  const blocos = produtos.filter(produto => {
    return produto.categories[0].id === 17;
  });
  const passaporte = produtos.filter(produto => {
    return produto.categories[0].id === 19;
  });

  return (
    <MDBAnimation type="slideInLeft">
      <MDBRow>
        <MDBCol>
          <BarraServicos />
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <MDBCol>
          <FoliaCarousel
            titulo="CAMAROTES"
            dados={camarotes}
            categoria="16"
            className="mt-4 mb-5"
          />
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <MDBCol>
          <FoliaCarousel
            titulo="BLOCOS"
            dados={blocos}
            categoria="17"
            className="mt-4 mb-5"
          />
        </MDBCol>
      </MDBRow>
      <MDBRow>
        <MDBCol>
          <FoliaCarousel
            titulo="PASSAPORTES DA FOLIA"
            dados={passaporte}
            categoria="18"
            className="mt-4 mb-5"
          />
        </MDBCol>
      </MDBRow>
      <h1 className="text-white">!!!</h1>
      <h1 className="text-white">!!!</h1>
      <AppFooter />
    </MDBAnimation>
  );
}
